@import '_bootstrap';

html {
	min-height: 100%;
	width: 100%; }

body {
	font: 500 16px/24px Ubuntu, Arial, sans-serif;
	color: $text-color;
	min-width: 1210px; }

.container {
	max-width: 1210px !important; }

.slick-slider {
	& * {
		&:active,
		&:focus {
			outline: none; } } }

.slick-loading .slick-list {
	background: url('../images/ajax-loader.gif') center center no-repeat; }

.lg-backdrop {
	background-color: rgba(#000, .7) !important; }

h2 {
	text-align: center;
	font-size: 40px;
	color: #000; }

h3 {
	color: #000;
	margin-bottom: 20px; }

a {
	&:hover {
		text-decoration: none; } }

.main-header {
	position: relative;
	min-height: 820px;

	&:before {
		content: '';
		position: absolute;
		background: url(../images/header-bg.png) 100% 100% no-repeat;
		height: 400px;
		width: 100%;
		z-index: 10; }

	&__logo {
		position: absolute;
		top: 110px;
		z-index: 11;
		width: 1150px;
		left: 50%;
		margin-left: -575px; }

	&__content {
		position: absolute;
		top: 280px;
		width: 1150px;
		left: 50%;
		margin-left: -575px;
		z-index: 99;
		text-align: center;
		color: #fff;
		height: 260px;
		overflow: hidden;

		h2 {
			font-size: 65px;
			font-weight: 700;
			margin-bottom: 60px;
			color: #fff; }

		p {
			font-size: 24px;
			line-height: 36px;
			font-weight: 500;
			height: 110px;
			overflow: hidden;
			margin-bottom: 0; } }

	.btn-more {
		position: absolute;
		bottom: 15px;
		left: 50%;
		transform: translateX(-50%); } }

.main-menu {
	position: absolute;
	width: 100%;
	z-index: 99;
	height: 73px;

	&:after {
		content: '';
		position: absolute;
		height: 1px;
		background-color: #307edf;
		left: 0;
		right: 0;
		bottom: 0; }

	ul {
		max-width: 1140px;
		margin: 0 auto;
		display: flex;
		height: 100%;
		justify-content: space-between;
		list-style: none;
		padding: 0;

		li {
			height: 100%; }

		a {
			display: flex;
			align-items: center;
			height: 100%;
			position: relative;
			font-size: 20px;
			color: $link-light;

			&:after {
				content: '';
				position: absolute;
				background-color: transparent;
				left: 0;
				right: 0;
				bottom: 0;
				height: 1px;
				z-index: 1;
				transition: background-color .3s ease-in-out; }

			&:hover {
				text-decoration: none;

				&:after {
					background-color: $color-orange; } } } } }

.slick-slider.slick-dotted {
	margin-bottom: 0; }

.main-slider {
	height: 820px;

	.slick-arrow {
		display: none !important; }

	&__item {
		min-height: 820px;
		position: relative;
		justify-content: center;
		align-items: center;
		background: 50% 0 / cover no-repeat;

		&:before {
			content: '';
			position: absolute;
			left: 0;
			right: 0;
			bottom: 0;
			top: 0;
			background-color: rgba(0,0,0, .4); } }

	&__content {
		height: 820px;
		max-width: 1180px;
		margin: 0 auto;
		display: flex;
		align-items: center;
		justify-content: center; } }


.btn-more {
	display: flex;
	justify-content: center;

	a {
		border: 0;
		min-width: 270px;
		padding: 0 40px;
		color: #fff;
		height: 60px;
		border-radius: 30px;
		font-size: 18px;
		display: flex;
		align-items: center;
		justify-content: center;
		cursor: pointer;
		background: rgba(255,97,55,1);
		background: -moz-linear-gradient(top, rgba(255,97,55,1) 0%, rgba(255,56,2,1) 100%);
		background: -webkit-gradient(left top, left bottom, color-stop(0%, rgba(255,97,55,1)), color-stop(100%, rgba(255,56,2,1)));
		background: -webkit-linear-gradient(top, rgba(255,97,55,1) 0%, rgba(255,56,2,1) 100%);
		background: -o-linear-gradient(top, rgba(255,97,55,1) 0%, rgba(255,56,2,1) 100%);
		background: -ms-linear-gradient(top, rgba(255,97,55,1) 0%, rgba(255,56,2,1) 100%);
		background: linear-gradient(to bottom, rgba(255,97,55,1) 0%, rgba(255,56,2,1) 100%);
		filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ff6137', endColorstr='#ff3802', GradientType=0 );

		&:hover,
		&:focus {
			outline: none; } }

	&__img {
		margin-top: 45px;
		height: 70px;
		background: url(../images/icons/mouse.png) 50% 0 no-repeat; } }


.about-info {
	padding-top: 65px;
	min-height: 600px;
	background: #fff url(../images/bubbles.png) 50% 0 no-repeat;

	&__content {
		padding-top: 35px; }

	p {
		margin-bottom: 24px; } }

.about-slider {
	background: rgba(255,97,55,1);
	background: -moz-linear-gradient(top, rgba(255,97,55,1) 0%, rgba(255,56,2,1) 100%);
	background: -webkit-gradient(left top, left bottom, color-stop(0%, rgba(255,97,55,1)), color-stop(100%, rgba(255,56,2,1)));
	background: -webkit-linear-gradient(top, rgba(255,97,55,1) 0%, rgba(255,56,2,1) 100%);
	background: -o-linear-gradient(top, rgba(255,97,55,1) 0%, rgba(255,56,2,1) 100%);
	background: -ms-linear-gradient(top, rgba(255,97,55,1) 0%, rgba(255,56,2,1) 100%);
	background: linear-gradient(to bottom, rgba(255,97,55,1) 0%, rgba(255,56,2,1) 100%);
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ff6137', endColorstr='#ff3802', GradientType=0 );
	border-radius: 10px;

	&__item {
		min-height: 270px;
		overflow: hidden;
		color: #fff;
		font-size: 18px;
		line-height: 26px;
		padding: 30px 16px;
		font-weight: 400;
		text-indent: 50px;
		background: url(../images/icons/left-quote.svg) 20px 20px / 30px no-repeat;

		p {
			margin-bottom: 0; } }

	.slick-arrow {
		top: calc(100% - 25px);
		width: 28px;
		height: 28px;

		&:before {
			content: none; }

		&.slick-next {
			right: 100px;
			background: url(../images/icons/right-arrow.svg) 0 0 no-repeat; }

		&.slick-prev {
			left: 100px;
			background: url(../images/icons/left-arrow.svg) 0 0 no-repeat;
			z-index: 9; } } }



.projects {
	padding-top: 45px;

	h2 {
		margin-bottom: 85px; }

	.work-hydro,
	.save-hydro {
		padding-left: 56px;
		height: 36px;
		display: flex;
		align-items: center;
		color: #fff; }

	.work-hydro {
		background: url(../images/icons/work_hydro.svg) 0 50% / 36px no-repeat; }

	.save-hydro {
		background: url(../images/icons/save_hydro.svg) 0 50% / 36px no-repeat; } }

.tab-pane {
	padding: 45px 0 0;

	&__content {
		padding-top: 15px;
		font-weight: 400; }

	.nav-item {
		width: 25%;
		height: 270px; }

	.nav-link {
		border: 0;
		margin: 0 auto;
		font-size: 22px;
		color: #787878;
		text-align: center;
		display: flex;
		flex-flow: column;
		width: 270px;
		height: 270px;
		align-items: center;
		justify-content: center;


		&.active {
			border: 1px solid #eeeeee;
			border-radius: 5px;
			color: #000000; } } }

.nav-tabs {
	border: 0;
	&--main {
		display: flex;
		justify-content: space-between; } }

.nav-item {
	&--main {
		width: 48%;
		.nav-link {
			width: 100%;
			text-align: left;
			background-color: #bebebe;
			border: 0;
			font-size: 24px;
			color: #fff;
			height: 70px;
			border-radius: 10px;
			display: flex;
			align-items: center;
			padding: 0 30px 0 25px;
			cursor: pointer;
			position: relative;

			&:after {
				content: '';
				position: absolute;
				width: 16px;
				height: 16px;
				top: 50%;
				margin-top: -8px;
				right: 30px;
				background: url(../images/icons/arrow.svg) 0 50% / contain no-repeat; }

			&:focus {
				outline: none; }

			&.active {
				background-color: #ff3702;

				&:after {
					transform: rotate(-180deg); } } }

		img {
			height: 36px;
			margin-right: 20px; } } }



.tab-pane {
	p {
		line-height: 30px;
		margin-bottom: 30px; } }

.tabs-img-list {
	display: flex;
	flex-wrap: wrap;
	margin-top: 15px;
	margin-left: -30px;

	.tabs-img {
		margin: 30px 0 0 30px;
		width: 270px;
		height: 160px;

		img {
			max-width: 100%; } }

	.tabs-img-big {
		width: 100%;
		margin: 0 0 0 30px;

		img {
			max-width: 100%; } } }


.content-slider {
	position: relative;

	&:before {
		content: '';
		position: absolute;
		background: url(../images/content-slider-bg.png) 50% 0 no-repeat;
		height: 150px;
		width: 100%;
		z-index: 10;
		top: -1px; }

	&__header {
		position: absolute;
		width: 100%;
		text-align: center;
		top: 230px;
		z-index: 9;

		h3 {
			color: #f5e0cc;
			font-size: 40px;
			font-weight: 500; } }

	&__item {
		position: relative;
		min-height: 740px;
		justify-content: center;
		align-items: center;
		background: 50% 0 / cover no-repeat;

		&:before {
			content: '';
			position: absolute;
			top: 0;
			bottom: 0;
			right: 0;
			left: 0;
			background-color: rgba(0,0,0, .4); } }

	&__text {
		max-width: 1180px;
		margin: 360px auto 0 auto;
		text-align: center;
		color: #fff;
		overflow: hidden;
		position: relative;

		h2 {
			font-size: 60px;
			margin-bottom: 50px;
			font-weight: 500;
			color: #fff; }

		p {
			font-size: 24px;
			line-height: 30px;
			font-weight: 400;
			overflow: hidden;
			margin-bottom: 0; } }

	.slick-arrow {
		width: 50px;
		height: 70px;
		top: 58%;
		z-index: 99;

		&:before {
			content: none; }

		&.slick-next {
			right: 8%;
			background: url(../images/icons/right-arrow-angle.svg) 0 0 / contain no-repeat; }

		&.slick-prev {
			left: 8%;
			background: url(../images/icons/left-arrow-angle.svg) 0 0 / contain no-repeat; } } }


.social-posts {
	padding: 70px 0 60px;
	min-height: 1200px;

	&__content {
		width: 80%;
		margin: 0 auto;
		text-align: center; } }

.social-links {
	display: flex;
	justify-content: center;
	margin: 45px 0 30px;

	&__item {
		display: flex;
		width: 28px;
		height: 28px;
		margin: 0 5px;

		div {
			width: 100%;
			height: 100%; } } }

.social-ok {
	background: url(../images/icons/social-ok.svg) 0 0/contain no-repeat; }

.social-vk-or {
	background: url(../images/icons/social-vk-or.svg) 0 0/contain no-repeat; }

.social-vk {
	background: url(../images/icons/social-vk.svg) 0 0/contain no-repeat; }

.social-fb-or {
	background: url(../images/icons/social-fb-or.svg) 0 0/contain no-repeat; }

.social-fb {
	background: url(../images/icons/social-fb.svg) 0 0/contain no-repeat; }

.social-tw-or {
	background: url(../images/icons/social-tw-or.svg) 0 0/contain no-repeat; }

.social-tw {
	background: url(../images/icons/social-tw.svg) 0 0/contain no-repeat; }


.social-posts-list {
	display: flex;
	flex-wrap: wrap; }

.social-post-sm {
	border: 1px solid #bababa;
	border-radius: 5px;
	padding: 16px 24px;
	display: flex;
	flex-flow: column;
	justify-content: space-around;
	min-height: 450px;
	margin-top: 25px;

	&__img {
		border-radius: 5px;
		height: 200px;
		background: 50% 50% / cover no-repeat;
		margin-bottom: 20px; }

	&__text {
		color: #000;
		line-height: 20px;
		height: 140px;
		overflow: hidden;
		margin-bottom: 26px;

		p {
			margin-bottom: 0; }

		.dots {
			position: relative;

			&:after {
				content: '...';
				color: $link-contrast;
				font-size: 30px;
				margin-left: 5px;
				line-height: .4; } } }

	&__source {
		margin-top: auto;
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		color: #6e6e6e;
		line-height: 1;
		height: 30px;
		overflow: hidden;

		.social-icon {
			width: 28px;
			height: 28px;
			margin-right: 16px; }

		span {
			width: calc(100% - 46px); } } }

.more-posts {
	margin-top: 60px;
	background-color: #e1e1e1;
	border-radius: 5px;
	display: flex;
	align-items: center;
	justify-content: center;
	width: 100%;
	height: 60px;
	cursor: pointer;
	border: 0;
	color: #6e6e6e; }

.main-footer {
	background-color: #eaeaea;
	min-height: 330px;
	position: relative;
	padding-bottom: 30px;

	&:before {
		content: '';
		position: absolute;
		width: 100%;
		z-index: 1;
		height: 190px;
		background: url(../images/footer-bg.png) 0 0 no-repeat; }

	&__content {
		padding-top: 200px; } }


.footer-banner {
	color: #0f233f;
	border: 1px solid #fff;
	border-radius: 5px;
	height: 160px;
	display: flex;
	align-items: center;
	padding: 20px 40px 20px 200px;

	&--1 {
		background: url(../images/icons/footer_1.svg) 40px 50% / 115px no-repeat; }

	&--2 {
		background: url(../images/icons/footer_2.svg) 40px 50% / 115px no-repeat; } }


.icon {
	width: 130px;
	height: 130px;
	display: block;
	margin-bottom: 20px;

	&--1 {
		background: url(../images/icons/icon-1.svg) 50% 50% / contain no-repeat; }

	&--2 {
		background: url(../images/icons/icon-2.svg) 50% 50% / contain no-repeat; }

	&--3 {
		background: url(../images/icons/icon-3.svg) 50% 50% / contain no-repeat; }

	&--4 {
		background: url(../images/icons/icon-4.svg) 50% 50% / contain no-repeat; } }

.nav-link {
	&.active {
		.icon {
			&--1 {
				background-image: url(../images/icons/icon-1-active.svg); }
			&--2 {
				background-image: url(../images/icons/icon-2-active.svg); }
			&--3 {
				background-image: url(../images/icons/icon-3-active.svg); }
			&--4 {
				background-image: url(../images/icons/icon-4-active.svg); } } } }

.info-bg {
	min-height: 240px;
	display: flex;
	flex-flow: column;
	justify-content: center;
	padding: 50px 30px;
	border-radius: 10px;
	background: url(../images/tabs.png) 50% 0 no-repeat;

	p {
		&:last-child {
			margin-bottom: 0; } } }
